import smoothScroll from './smooth-scroll';

/**
 * Smooth scroll internal links
 */
export default () => {
  $('html').on('click', 'a[href^="#"]:not([href="#"],[href="#maincontent"],.anchor-jump-link,.coveo-search-result-link)', (e) => {
    e.preventDefault();
    const target = e.currentTarget.hash,
          $target = $(target);
    if ($target.length) {
      smoothScroll($target);
    }
  });
};
