// -----------------------------------------------------------------------------
// Component Includes
// -----------------------------------------------------------------------------
import extendJquery from '@plugins/extend-jquery';
import Animate from '@plugins/animate';
import loading from '@plugins/loading';
// import FacetedSearch from '@components/faceted-search';

import siteHeader from '@components/site-header-new';
import mobileMenu from '@components/mobile-menu';
import mobileExploreMenu from '@components/mobile-explore-menu';
import socialShare from '@components/social-share';
import formValidation from '@plugins/form-validation';
import anchorSmoothScroll from '@plugins/anchor-smooth-scroll';
import jsLinkEvent from '@plugins/js-link-event';
import heroHome from './components/heros/hero-home/hero-home';
import accordion from '@components/accordion';
import accordionTabs from '@components/accordion-tabs';
import contentExpander from '@components/content-expander';
import videoModal from '@components/video-modal';
import membershipBenefits from '@components/membership-benefits';
import testimonialSlider from '@components/testimonial';
import alertBanner from '@components/alert-banner';
import scrollingBanner from '@components/scrolling-banner';
import gatedContentPromo from '@components/gated-content-promo';
import coveoSearch from '@components/coveo';
import memberBenefitsGrid from '@components/member-benefits-grid';
import capabilityModel from '@components/capability-model';

window.$ = jQuery;

(($) => {

  // Document Ready
  $(() => {

    // -----------------
    // High-priority JS
    // -----------------
    extendJquery();
    new Animate('[data-animation]');
    loading();

    // if ($('.faceted-search').length) {
    //   new FacetedSearch().init();
    // }

    // -----------------
    // Low-priority JS
    // -----------------
    siteHeader();
    mobileMenu();
    mobileExploreMenu();
    socialShare();
    formValidation();
    anchorSmoothScroll();
    jsLinkEvent();
    heroHome();
    accordion();
    accordionTabs();
    contentExpander();
    videoModal();
    membershipBenefits();
    testimonialSlider();
    alertBanner();
    scrollingBanner();
    gatedContentPromo();
    memberBenefitsGrid();
    coveoSearch();
    capabilityModel();

  });

})($);


// -----------------------------------------------------------------------------
// Service Worker registration
// -----------------------------------------------------------------------------
if ('serviceWorker' in navigator && process.env.USE_SERVICEWORKER !== false) {
  window.addEventListener('load', () => {
    navigator.serviceWorker
      .register('/service-worker.js', {
        scope: '/'
      });
    // .then((registration) => { })
    // .catch((registrationError) => { });
  });
}
